
html,body{
    
    width: 100%;
}
body{
    
    background-repeat: no-repeat;
    background-size: cover;
    width: 100%;
    height: 100%;
}
*{padding: 0; margin: 0; box-sizing: border-box;}

header {
	background: url('http://www.autodatz.com/wp-content/uploads/2017/05/Old-Car-Wallpapers-Hd-36-with-Old-Car-Wallpapers-Hd.jpg');
	text-align: center;
	width: 100%;
	height: auto;
	background-size: cover;
	background-attachment: fixed;
	position: relative;
	overflow: hidden;
	border-radius: 0 0 90% 90% / 70%;
}
header .overlay{
	width: 100%;
	height: 100%;
	padding: 20px;
	color: #FFF;
	/* text-shadow: 1px 1px 1px #333; */
    color: #cfc547;

    color: #fff;
    text-shadow: 0 0 10px #fff,
                 0 0 20px #fff, 
                 0 0 40px #fff;
    font-size: 40px;
    font-weight: bold;
    font-family: monospace;
    letter-spacing: 7px;
    cursor: pointer;

text-shadow: 3px 0px 7px rgba(81,67,21,0.8), 
	  -3px 0px 7px rgba(81,67,21,0.8), 
	  0px 4px 7px rgba(81,67,21,0.8);
  background-image: linear-gradient( 135deg, #9f05ff69 10%, #fd5e086b 100%);
	
}

h1 {
	font-family: 'Dancing Script', cursive;
	font-size: 50px;
	margin-bottom: 10px;
}

@media screen and (max-width: 650px){
    
    body{
        /* background-color: #c78a6a6c; */
        /* background-image: linear-gradient(#FFFFFF, rgb(255, 122, 89)); */
        background-image: url("./images/housebg3.jpg");
    }
    .container{
        width: 75vw;
    }
    .est{
        width: 50vw;
    }
  }
@media screen and (min-width: 768px){
    
    body{
        background-image: url("./images/housebg3.jpg");
    }
    .container{
        width: 50vw;
    }
    .est{
        width: 30vw;
    }
  }

  .container{
    background-color: rgb(184, 51, 51,0.5);
    height: 75%;
    
    margin: auto;
    margin-top: 30px;
    border: 2px solid black;
    border-radius: 10px;
    
}

.button-72 {
    align-items: center;
    background-color: initial;
    background-image: linear-gradient(rgba(179, 132, 201, .84), rgba(57, 31, 91, .84) 50%);
    border-radius: 42px;
    border-width: 0;
    box-shadow: rgba(57, 31, 91, 0.24) 0 2px 2px,rgba(179, 132, 201, 0.4) 0 8px 12px;
    color: #FFFFFF;
    cursor: pointer;
    display: flex;
    font-family: Quicksand,sans-serif;
    font-size: 14px;
    font-weight: 400;
    justify-content: center;
    letter-spacing: .04em;
    line-height: 15px;
    margin: 0;
    padding: 12px 12px;
    text-align: center;
    text-decoration: none;
    text-shadow: rgba(255, 255, 255, 0.4) 0 0 4px,rgba(255, 255, 255, 0.2) 0 0 12px,rgba(57, 31, 91, 0.6) 1px 1px 4px,rgba(57, 31, 91, 0.32) 4px 4px 16px;
    user-select: none;
    -webkit-user-select: none;
    touch-action: manipulation;
    vertical-align: baseline;
    
    color: #fff;
    text-shadow: 0 0 10px #fff,
                 0 0 20px #fff, 
                 0 0 40px #fff;
  }
  
  .button-72:hover {
    background-image: linear-gradient(#B384C9, #391F5B 50%);
  }
  
  @media (min-width: 768px) {
    .button-72 {
      font-size: 15px;
      padding: 12px 20px;
    }
  }

